<template>
  <Header />
  <div>
 <button @click="ToggleAdd" :class= "ShowAdd ? 'fa-solid fa-minus' :'fa-solid fa-plus' "></button>
  </div>
  <br />
  <div v-if="ShowAdd">
  <AddGrocery @add-list="addList"/>
  </div>
  <List :list= "list" @delete-list = "deleteList"
   @play-reminder ='playReminder' />
</template>

<script>
import Header from './components/Header.vue'
import List from './components/List.vue'
import AddGrocery from './components/AddGrocery.vue'

export default {
  name: 'App',
  components: {
    Header,
    List,
    AddGrocery,
},

  data() {
    return {
      list: [],
      ShowAdd : false
    }
    
  },

  methods: {
    addList(newList){
      this.list = [...this.list, newList]
    },
    ToggleAdd() {
      this.ShowAdd = !this.ShowAdd
    },
    deleteList(id) {
     if(confirm('Are you sure!')) {
        this.list = this.list.filter((item) => 
      item.id !== id)
      }
    },
    playReminder(id) {
     this.list = this.list.map((item) =>
      item.id === id ? {
      ...item, reminder : !item.reminder
      } : item)
      }, 
      
    },

  created () {
    this.list = [{
id: 1,
      text: 'tomatoes',
      store: 'Aldi',
      reminder: true
    },
    {
id: 2,
      text: 'Onions',
      store: 'Tesco',
      reminder: true
    },
    {
id: 3,
      text: 'Detergent',
      store: 'Iceland',
      reminder: false
    }

    ]
  }

}
</script>

